<template>
    <el-container style="height: 500px; border: 1px solid #eee">
        <el-header class="el-icon-s-home" style=" font-size: 40px; background-color:#409EFF; color:azure">
            教考系统</el-header>
        <el-footer>
            <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
                <el-menu-item index="1"><router-link to="/teacher" tag="span">账号信息</router-link></el-menu-item>
                <el-menu-item index="2"><router-link to="/questionBank" tag="span">题库管理</router-link></el-menu-item>
                <el-menu-item index="3"><router-link to="/examPaper" tag="span">试卷管理</router-link></el-menu-item>
                <el-menu-item index="4"><router-link to="/examSchedule" tag="span">考试安排</router-link></el-menu-item>
                <el-menu-item index="4"><router-link to="/examGrading" tag="span">阅卷</router-link></el-menu-item>
                <el-menu-item index="4"><router-link to="/class" tag="span">班级管理</router-link></el-menu-item>
                <el-menu-item index="5"><router-link to="/studentLogin" tag="span">退出登录</router-link></el-menu-item>
            </el-menu>
        </el-footer>


        <el-main>

            <el-form :model="inputData" :inline="true" label-width="50px" style="width: 500px;"
                class="demo-form-inline">
                <el-form-item prop="key">
                    <el-input v-model="inputData" placeholder="请输入关键字以搜索"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="likeGet">查询</el-button>
                </el-form-item>
                <!-- <el-form-item>
                    <el-button type="primary" @click="dialogFormVisible2 = true">添加</el-button>
                </el-form-item> -->
            </el-form>



            <el-table :data="examData" style="width: 100%">
                <el-table-column prop="examNum" label="试卷编号" width="300">
                </el-table-column>
                <el-table-column prop="course.courseName" label="所属课程" width="300">
                </el-table-column>
                <el-table-column prop="course.courseNum" label="课程编号" width="300">
                </el-table-column>
                <el-table-column prop="teacher.teacherName" label="组卷人" width="300">
                </el-table-column>
                <el-table-column label="操作" width="300">
                    <template slot-scope="scope">
                        <el-button type="text" @click="dialogFormVisible = true">修改</el-button>
                        <el-dialog title="修改" :visible.sync="dialogFormVisible">
                            <el-form :model="form">
                                <el-form-item label="所属课程" :label-width="formLabelWidth">
                                    <el-input v-model="form.course.courseName" autocomplete="off"></el-input>
                                </el-form-item>
                                <el-form-item label="课程编号" :label-width="formLabelWidth">
                                    <el-input v-model="form.course.courseNum" autocomplete="off"></el-input>
                                </el-form-item>
                                <el-form-item label="组卷人" :label-width="formLabelWidth">
                                    <el-input v-model="form.teacher.teacherName" autocomplete="off"></el-input>
                                </el-form-item>
                            </el-form>
                            <div slot="footer" class="dialog-footer">
                                <el-button @click="reset">取 消</el-button>
                                <el-button type="primary" @click="formUpdate">确 定</el-button>
                            </div>
                        </el-dialog>
                        <el-button @click="deleteById(scope.row.examNum)" type="text" size="medium">删除</el-button>
                        <el-button @click="viewExam(scope.row.examNum)" type="text" size="medium">查看</el-button>
                    </template>
                </el-table-column>


            </el-table>
            <!-- 对话框，移出 el-form-item -->
            <!-- <el-dialog title="添加" :visible.sync="dialogFormVisible2">
                <el-form :model="form2">
                    <el-form-item label="所属课程" :label-width="formLabelWidth">
                        <el-input v-model="form2.courseNum" autocomplete="off"></el-input>
                    </el-form-item>

                    <el-form-item label="课程编号" :label-width="formLabelWidth">
                        <el-input v-model="form2.course.courseNum" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="组卷人" :label-width="formLabelWidth">
                        <el-input v-model="form2.teacher.teacherName" autocomplete="off"></el-input>
                    </el-form-item>
                </el-form>

                <div slot="footer" class="dialog-footer">
                    <el-button @click="reset">取 消</el-button>
                    <el-button type="primary" @click="insertExam">确 定</el-button>
                </div>
            </el-dialog> -->
        </el-main>
    </el-container>
</template>

<script>
import axios from 'axios';
export default {
    data() {
        return {
            inputData: '',
            //绑定修改数据
            // form2: {
            //     id: '',
            //     course: '',
            //     courseId: '',
            // },
            //试卷详情
            // examData: {
            //     examNum: '',
            //     isReleased: '',
            //     teacher: [],
            //     course: [],
            //     modules: [],
            // },
            examData: [],
            //绑定添加数据
            form2: {
                examNum: '',
                isReleased: '',
                courseNum: '',
                teacherNum: '',
            },

            form: {
                examNum: '',
                isReleased: '',
                teacher: {

                    teacherName: '',
                },
                course: {
                    courseName: '',
                    courseNum: '',
                }
            },
            //试卷详情

            dialogTableVisible: false,
            dialogFormVisible: false,
            dialogFormVisible2: false,
            dialogVisible : false,
            formLabelWidth: '120px'
        }
    },
    methods: {
        handleClick(row) {
            console.log(row);
        },
        onSubmit() {
            console.log('submit!');
        },
        //跳转试卷详情页面并传递试卷编号
        viewExam(examId){
            this.$router.push({name: 'showExamPaper',params: {id:Number(examId)} } );
        },
        //查询全部
        getAll() {
            axios.get("/api/exam").then((result) => {
                this.examData = result.data.data;
            });
        },
        //模糊查询
        likeGet() {
            axios.get(`/api/exam/inputData/${this.inputData}`).then((result) => {
                this.examData = result.data.data;

                
            });

        },
        //修改表单
        formUpdate(id) {
            this.form.examNum=id;
            axios.post("/api/exam", this.form).then((result) => {
                if (result.data.code == 1) {
                    this.dialogFormVisible = false;
                    this.open2();

                }
                else {
                    this.open3();

                }
            });

        },
        //删除数据
        deleteById(id) {
            axios.delete(`/api/exam/${id}`).then((result) => {
                if (result.data.code == 1) {
                    this.open2();
                    this.getAll();

                }
                else {
                    this.open3();
                }


            });

        },
        //添加试卷
        insertExam() {
            axios.post("/api/exam", this.form2).then((result) => {
                if (result.data.code == 1) {

                    this.open2();

                }
                else {
                    this.open3();
                }

            });

        },
        //重置
        reset() {
            this.form = '';
            this.dialogFormVisible = false
        },
        //成功提示
        open2() {
            this.$message({
                message: '恭喜你，这是一条成功消息',
                type: 'success'
            });
        },
        //错误提示
        open3() {
            this.$message({
                message: '警告哦，这是一条警告消息',
                type: 'warning'
            });
        },

    },
    mounted() {
        this.getAll();

    },
}
</script>

<style scoped>
.el-container {
    min-height: 100vh; /* 至少占据整个视口高度 */
    max-height: 1200px; /* 最大高度限制 */
    overflow-y: auto; /* 如果内容超出高度，显示滚动条 */
    display: flex;
    flex-direction: column;
  }
  
  .el-header, .el-footer {
    flex: 0 0 auto; /* 固定高度 */
    background-color: #409EFF;
    color: azure;
    padding: 10px;
  }
  
  .el-main {
    flex: 1 1 auto; /* 自动占据剩余空间 */
    overflow-y: auto; /* 如果内容超出高度，显示滚动条 */
    padding: 20px;
    box-sizing: border-box;
  }
</style>

